/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

html {
  scroll-behavior: smooth;
  /*Comenta la línea (o dale el valor 'auto' a scroll behavior )
 para probar la diferencia con y sin scroll suavizado*/ }

@keyframes pop {
  0% {
    opacity: 0%; }
  5% {
    opacity: 50%; }
  10% {
    opacity: 0%; }
  15% {
    opacity: 50%; }
  20% {
    opacity: 15%; }
  30% {
    opacity: 0%; }
  40% {
    opacity: 50%; }
  80% {
    opacity: 80%; }
  90% {
    opacity: 0%; }
  100% {
    opacity: 100%; } }

@keyframes arrow1 {
  0% {
    transform: translate(0, 0) rotate(45deg); }
  50% {
    transform: translate(15px, 15px) rotate(45deg); }
  100% {
    transform: translate(0, 0) rotate(45deg); } }

@keyframes arrow2 {
  0% {
    transform: translate(0, 0); }
  50% {
    transform: translate(15px, 0); }
  100% {
    transform: translate(0, 0); } }

@keyframes arrow3 {
  0% {
    transform: translate(0, 0) rotate(90deg); }
  50% {
    transform: translate(0, 15px) rotate(90deg); }
  100% {
    transform: translate(0, 0) rotate(90deg); } }

