.hero {
  height: 100vh;
  width: 100%;
  position: relative;
  z-index: 3;
  background-color: #ebebeb; }
  .hero__grid {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto auto auto;
    justify-items: center;
    height: 80vh;
    width: 100%;
    position: relative;
    gap: 10px; }
  .hero__click {
    grid-column: 1/3;
    grid-row: 3/4;
    display: grid;
    grid-template-columns: 25px auto auto;
    grid-template-rows: 25px 25px 1fr 1fr;
    gap: 7px;
    justify-items: flex-end;
    align-items: flex-end;
    align-self: flex-end;
    font-size: 12px;
    width: 130px;
    color: #FEC260; }
    .hero__click--1 {
      width: 20px;
      grid-column: 1/2;
      grid-row: 1/2;
      animation: arrow1 2s infinite; }
    .hero__click--2 {
      width: 20px;
      grid-column: 1/2;
      grid-row: 2/3;
      animation: arrow2 2s infinite; }
    .hero__click--3 {
      width: 20px;
      grid-column: 2/3;
      grid-row: 1/2;
      animation: arrow3 2s infinite;
      align-self: center; }
  .hero__computer {
    width: 100px;
    height: 130px;
    grid-column: 2/4;
    grid-row: 2/5; }
  .hero__mac {
    position: absolute;
    width: 100px;
    filter: drop-shadow(2px -1px 3px #7d7d7d);
    cursor: pointer; }
  .hero__portrait {
    grid-column: 1/3;
    grid-row: 1/2;
    align-self: flex-end;
    display: flex;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin-bottom: -40px;
    margin-right: -60px; }
    .hero__portrait--photo {
      height: 100%;
