    @media all and (min-width: 768px) {
      .hero__portrait {
        width: 210px;
        height: 210px;
        margin-bottom: -40px;
        margin-right: -90px; } }
  .hero__article {
    grid-column: 1/3;
    grid-row: 2/3; }
  .hero__title {
    font-weight: bold;
    font-size: 1.4em;
    margin-bottom: 5px;
    font-family: "Fredoka One", cursive; }
    .hero__title--name {
      color: #55AE95; }
    @media all and (min-width: 768px) {
      .hero__title {
        font-size: 1.7em; } }
  .hero__text {
    font-size: 1em; }
    @media all and (min-width: 768px) {
      .hero__text {
        font-size: 1.2em; } }

